* {
    margin: 0;
    padding: 0;
}

html, body {
    overflow: hidden;
}

img {
    color: #669;
    font-family: 'Roboto Mono', monospace;
    font-weight: 300;
    font-size: 0.75em;
}

a {
    display: inline-block;
    color: #669;
    text-decoration: none;
    font-size: 1em;
    transition: transform 0.5s;
}

a:hover {
    transform: translateY(-0.25em);    
}

a:active {
    color: #edd;
}

p {
    color: #669;
    font-family: 'Roboto Mono', monospace;
    font-weight: 500;
    font-size: 1em;
    padding-bottom: 1em;
}

p i {
    font-size: 0.75em
}

h2 {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    padding-top: 1em;
    padding-bottom: 1em;
}

h3 {
    color: #669;
    font-family: 'Roboto Mono', monospace;
    font-weight: 500;
    font-size: 1em;
    padding-top: 2em;
    padding-bottom: 1em;
}

h3::before {
    content: ".";
}

h4 {
    color: #669;
    font-family: 'Roboto Mono', monospace;
    font-weight: 500;
    font-size: 1em;
    padding-top: 2em;
    padding-bottom: 1em;
}

.webgl {
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

.main {
    color: #669;
    display: flex;
    align-items: stretch;
    visibility: visible;   
}

.main h1{
    font-family: 'Poppins', sans-serif;
    font-size: 3em;
    font-weight: 600;
    line-height: 0.875em;
    padding: 0.5em 0 0 0;
}

.main h2{
    font-family: 'Poppins', sans-serif;
    font-size: 2em;
    font-weight: 600;
    line-height: 0.875em;
    padding: 0.75em 0 0.75em 0;
    opacity: 0.75;
}

.main p{
    font-weight: 300;
    font-size: 1em;
    padding: 0.75em 0 0.75em 0;
}

.box {
    position: absolute;
    width: 640px;
    height: 80vh;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: linear-gradient(135deg, #eef3, #fffc 50%, #eef3 100%);
    backdrop-filter: blur(10px);
    border-radius: 5px;
    box-shadow: #6666991a 0px 5px 10px 0px, #6666991a 0px 0px 0px 1px;
    z-index: 11;
    visibility: hidden;       
}

#mainIdiomChangers {
    visibility: hidden;
}

.container {
    padding: 40px 40px 40px 80px;
    height: calc(100% - 80px);    
}

.bar {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    height: 40px;
    font-family: 'Roboto Mono', sans-serif;
    font-weight: 300;
}

.en-changer, .fr-changer, .pt-changer, .closer{
    display: inline-block;
    font-weight: 500;
    color: #669;
    padding-bottom: 1em;
    transition: transform 0.5s, color 0.1s;
}

.en-changer, .fr-changer, .pt-changer{
    font-size: 0.75em;    
}

.closer {
    font-size: 1.5em;
}

.en-changer:hover, .fr-changer:hover, .pt-changer:hover {
    transform: translateY(-0.25em);
    cursor: pointer;    
}

.closer:hover {
    transform: translateY(-0.125em);
    cursor: pointer;
}

.en-changer:active, .fr-changer:active, .pt-changer:active, .closer:active {
    color: #edd;
}

.scroll {
    height: calc(100% - 40px);
    padding-right: 38px;
    overflow-x: hidden;
    overflow-y: auto;
    scroll-behavior: smooth;
    text-align: justify;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
    color: #669    
}

.scroll p {
    font-family: 'Roboto Mono', monospace;
    font-size: 0.75em;
    line-height: 2.25em;
}

.sphere-label {
    opacity: 0.25;
    color: #669;
    font-family: 'Roboto Mono', monospace;
    font-size: 1em;
    font-weight: 500;
    padding: 2px;
    transition: font-size 0.5s ease-in-out, opacity 0.25s ease-in-out, color 0.1s
}

.sphere-label:hover {
    opacity: 1;
    font-size: 1.25em;
    cursor: pointer;    
}

.sphere-label:active {
    color: #edd;   
}

.main-title {
    z-index: 10;
    position: absolute;
    top: 80px;
    left: 40px;
    width: 31vw;
}

.title-flex {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
}

.portrait {
    height: 135px;
    width: 135px;
    border-radius: 50%;
    box-shadow: #6666991a 0px 5px 10px 0px, #6666991a 0px 0px 0px 1px;
}

.social-icons-box{
    display: flex;
    justify-content: space-between;
    position: absolute;
    width: 130px;
    transform-origin: bottom right;
    color: #eef;
    font-family: 'Roboto Mono', monospace;
    bottom: 40px;
    right: 40px;
    z-index: 10;  
    visibility: hidden;  
}

.social-icons{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #eef;
    border-radius: 5px;
    font-size: 1.5em;
    min-width: 40px;
    height: 40px;
    transition: background-color 0.5s ease-in-out;
}

.social-icons i{
    color: #fff;
}

.social-icons:hover {
    background-color: #669;
}

.social-icons:active {
    background-color: #edd;
    transition: background-color 0.1s;
}

#backToBoxTop i {
    transition: transform 0.5s;
}

#backToBoxTop i:hover {
    transform: translateY(-0.25em);
    cursor: pointer
}

.percent-container {
    max-width: 480px;
    display: flex;
    flex-wrap: wrap;    
}

.percent-box {
    min-width: 160px;
    min-height: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    flex-direction: column;
}

.percent-box .percent {
    width: 120px;
    height: 120px;
    position:relative
}

.percent-box .percent svg {
    width: 120px;
    height: 120px;
    position:relative
}

.percent-box svg circle {
    width: 120px;
    height: 120px;
    stroke-width: 1px;
    fill: none;
    transform: translate(10px,10px);
    stroke-dasharray: 314.15;
    stroke-dashoffset: 314.15;    
}

.percent-box svg circle:nth-child(1) {
    stroke-dashoffset: 0;
    stroke: #6666991a;
}

.percent-box svg circle:nth-child(2) {
    stroke: #669;
}

.perc000 circle:nth-child(2) {
    stroke-dashoffset: calc((314.15 * 0.0 * var(--anim)) - 314.15);
}

.perc020 circle:nth-child(2) {
    stroke-dashoffset: calc((314.15 * 0.2 * var(--anim)) - 314.15);
}

.perc040 circle:nth-child(2) {
    stroke-dashoffset: calc((314.15 * 0.4 * var(--anim)) - 314.15);
}

.perc060 circle:nth-child(2) {
    stroke-dashoffset: calc((314.15 * 0.6 * var(--anim)) - 314.15);
}

.perc080 circle:nth-child(2) {
    stroke-dashoffset: calc((314.15 * 0.8 * var(--anim)) - 314.15);
}

.perc100 circle:nth-child(2) {
    stroke-dashoffset: calc((314.15 * 1.0 * var(--anim)) - 314.15);
}

.perc-label {
    top: 0;
    left: 0;
    width: 100px;
    height: 100px;
    padding: 10px 10px 10px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    color: #669;
    font-size: 0.75em;
    font-weight: 500;
    font-family: 'Roboto Mono', sans-serif;
    text-align: center;    
}

.perc-label a:hover {
    cursor: help;
}

.loader-box {
    width: 640px;
    height: 640px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 20;
    background-color: white;
    visibility: hidden;    
}

.loader-polyline {
    fill: none;
    stroke: #669;
    stroke-width: 16px;
    stroke-linejoin: bevel;    
    shape-rendering: geometricPrecision;    
    stroke-linecap: round;
    stroke-dasharray: 1767 1767;
    stroke-dashoffset: 0;
    animation: dash 10s linear infinite;
}

@keyframes dash {
    0% {
        stroke-dasharray: 0 3534;
        stroke-dashoffset: 0;        
    }
    25% {
        stroke-dasharray: 1767 1767;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 0 3534;
        stroke-dashoffset: -1767;
    }
    75% {
        stroke-dasharray: 1767 1767;
        stroke-dashoffset: -1767;
    }
    100% {
        stroke-dasharray: 0 3534;
        stroke-dashoffset: -3534;
    }
}



::-moz-selection { /* Code for Firefox */
    color: #669;
    background: #edd;
}
  
::selection {
    color: #669;
    background: #edd;    
}

::-webkit-scrollbar {
    width: 2px;
}

::-webkit-scrollbar-track {
background-color: #6666991a;  
}

::-webkit-scrollbar-thumb {
background: #669;
}

:root {
    --anim: 1
}